.container {
  height: 100%; 
  display: -ms-flexbox; 
  display: flex; 
  -ms-flex-flow: row nowrap; 
      flex-flow: row nowrap;
}

ul {
  list-style-type: none;
}

.menu {
    margin-right: 1rem;
    display: -ms-flexbox;
    display: flex;
    -ms-flex: 1 1;
        flex: 1 1;
    -ms-flex-pack: end;
        justify-content: flex-end;
    color: aliceblue;
}

.menu > * {
  padding: 2rem 0;
  margin: 0 6rem;
  font-size: 1.25rem;
  font-weight: 450;
  border-bottom: 2px solid transparent;

}

.menu > *:hover {
  cursor: pointer;
}


