.container {
  min-height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex:auto;
      flex:auto;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  padding: 1.25rem 3.5rem 2rem;
  text-align: center;
  
}
.container_title {
    font-family: "Times New Roman", Times, serif;
    font-weight: bold;
    font-size:  2.5rem; 
    color: aliceblue;
    padding-bottom: 1rem; 
}

.container_text{
    font-family: "Times New Roman", Times, serif;
    font-weight: 450;
    font-size: 1.5rem;
    color: aliceblue;
    padding-bottom: 1rem; 
}

